import React from "react";
import "./VideoBackground.css";
import videoSource from "./Coming_soon.MP4";

// Import Custome Hooks
import useAxios from "../CustomeHooks/useAxios/useAxios";
// import usePreventRouterLinks from "../CustomeHooks/usePreventRouterLinks/usePreventRouterLinks";

const VideoBackground = () => {
  const {
    data: { image = "" },
  } = useAxios(`${process.env.PUBLIC_URL}/apis/logo.json`, []);

  return (
    <div className="video-background">
      <video autoPlay loop muted>
        <source src={videoSource} type="video/mp4" />
      </video>
      <div className="text-logo-container">
        <img src={image} alt="Logo" draggable="false" className="logo" />
      </div>
    </div>
  );
};

export default VideoBackground;
